* {
    font-family: 'Spectral', serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

h1,
h2,
h3,
h4,
p,
ul,
li {
    margin: 0;
    color: var(--font-dark);
}

ul {
    list-style-type: none;

    & li {
        font-size: inherit;
        font-style: italic;

        & a {
            font-size: inherit;
            color: inherit;
            text-decoration: none;
            position: relative;

            &::before {
                content: '';
                position: absolute;
                background: linear-gradient(to right, #CCB76C 20%, #D1B464);                left: -0.05em;
                top: 30%;
                bottom: 30%;
                width: 0;
                transition: right 300ms ease-out;
                right: 100%;
                transition: right 300ms ease-out;
                transform: skewX(-13deg);
                z-index: -1;

            }

            &:hover::before {
                width: auto;
                right: -0.2em;
            }
        }
    }
}

.new-tab {
    position: relative;
    display: flex;

    &::after {
        content: '';
        @include box(0.6em);
        // border: 3px solid black;
        margin-left: auto;
        margin-top: auto;
        margin-bottom: auto;
        margin-right: 0.4em;
        background: black;
    }

    &::before {
        font-family: sans-serif;
        content: "\2191";
        position: absolute;
        right: 0;
        top: 50%;
        transform: translate3d(-1px, -84.1%, 0)rotate(45deg);
    }


}

.is-locked {
    position: relative;
    display: flex;

    &::before {
        font-family: sans-serif;
        content: '\222a';
        position: absolute;
        right: 1.4%;
        top: 20%;
        font-size: 41px;
        font-style: normal;
        transform: rotate(180deg);
    }

    &::after {
        content: '';
        width: 0.2em;
        height: 0.1em;
        border: 7px solid black;
        margin-left: auto;
        margin-top: auto;
        margin-bottom: 0;
        margin-right: 0.3em;
    }
}




.Type-1 {
    font-size: 5.8rem;
    line-height: 0.9;
    letter-spacing: -0.05em;
}

.Type-2 {
    font-size: 1.9rem;
    font-weight: 300;
    text-transform: uppercase;
    letter-spacing: 0.05em;
}

.Type-3 {
    font-size: 3rem;
    line-height: 0.9;
    letter-spacing: -0.02em;
}